div.word {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 32px;
    min-height: 32px;
    max-height: 32px;
    margin: 2px;
    border: 1px solid rgba(0,0,0,.3);
    border-radius: 4px;
    padding: 0 8px;
    background: #fff;
    color: #000;
    cursor: pointer;
    /*
    opacity: 0;
    transition: opacity .6s;
    */
}
/*
div.word.show {
    opacity: 1;
    transition: opacity .6s;
}
*/
div.word > div.types {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 40px;
}

div.word > div.name {
    font-weight: bold;
    font-size: 120%;
}

div.word > div.types > div {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    width: 10px;
    height: 10px;
    margin: 1px;
    font-size: 8px;
    font-weight: bold;
    color: #000;
    background: #aaa;
}

div.word > div.types > div.grey { opacity: .4 }

div.word > div.types > div.noun        { background: #77f }
div.word > div.types > div.adjective   { background: #ff7 }
div.word > div.types > div.verb        { background: #f77 }
div.word > div.types > div.conjunction { background: #f80 }
div.word > div.types > div.preposition { background: #f7f }
div.word > div.types > div.adverb      { background: #7f7 }
