body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  background-color: #567;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 12px;
  color: white;
}

div.icon-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
    margin: 1rem;
    padding: 8px;
    box-shadow: 0 4px 8px rgba(0,0,0,.5);
    background: #fff;
}

@-webkit-keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

@keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

.tfw-view-icon {
    display: inline-block;
    transition: all .3s;
    margin: 0;
    padding: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.tfw-view-icon.zero {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
}

.tfw-view-icon.active {
    cursor: pointer;
}

.tfw-view-icon.animate {
    animation: tfw-view-icon-rotation 1.5s linear infinite;
    -webkit-animation: tfw-view-icon-rotation 1.5s linear infinite;
}

.tfw-view-icon.flipH {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1)
}

.tfw-view-icon.flipV {
    -webkit-transform: scaleY(-1);
            transform: scaleY(-1)
}

.tfw-view-icon.flipH.flipV {
    -webkit-transform: scale(-1, -1);
            transform: scale(-1, -1)
}

div.word-list {
    overflow: auto;
    padding: 0;
    margin: 1rem;
    box-shadow: 0 4px 8px #000;
    background: #ccc;
    min-width: 280px;
    max-height: 95vh;
    /* To prevent a bug in Google Chrome */
    overflow-anchor: none;
}

div.word-list > div {
    margin: 0;
    display: block;
    width: 100%;
    min-width: 100%;
    background-repeat: repeat-y;
    background-position: 0 0;
    background-size: 263px 36px;
    background-image: url(data:image/jpeg;base64,);
}

div.word-list > div.body {
    overflow: hidden;
}


div.word-list > div.space {
    font-size: 0;
    content: '.';
}

div.word-list-wait {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    min-width: 280px;
    height: 280px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

div.word {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 32px;
    min-height: 32px;
    max-height: 32px;
    margin: 2px;
    border: 1px solid rgba(0,0,0,.3);
    border-radius: 4px;
    padding: 0 8px;
    background: #fff;
    color: #000;
    cursor: pointer;
    /*
    opacity: 0;
    transition: opacity .6s;
    */
}
/*
div.word.show {
    opacity: 1;
    transition: opacity .6s;
}
*/
div.word > div.types {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    max-width: 40px;
}

div.word > div.name {
    font-weight: bold;
    font-size: 120%;
}

div.word > div.types > div {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 100%;
    width: 10px;
    height: 10px;
    margin: 1px;
    font-size: 8px;
    font-weight: bold;
    color: #000;
    background: #aaa;
}

div.word > div.types > div.grey { opacity: .4 }

div.word > div.types > div.noun        { background: #77f }
div.word > div.types > div.adjective   { background: #ff7 }
div.word > div.types > div.verb        { background: #f77 }
div.word > div.types > div.conjunction { background: #f80 }
div.word > div.types > div.preposition { background: #f7f }
div.word > div.types > div.adverb      { background: #7f7 }

